export const RefreshDocument = gql`
    mutation Refresh {
  __typename
  refresh {
    __typename
    accessToken
    refreshToken
  }
}
    `;
export const CompanyFilterDocument = gql`
    query CompanyFilter($searchTerm: String) {
  companies(filter: {searchTerm: $searchTerm}) {
    id
    commonName
    instruments {
      id
      isPrimary
      quotes {
        id
        isPrimary
        localTicker
        exchange {
          id
          shortName
          country {
            id
            countryCode
          }
        }
      }
    }
  }
}
    `;
export const CompanyResolutionDocument = gql`
    query CompanyResolution($identifier: String!) {
  companies(
    filter: {resolution: {identifier: $identifier, identifierType: unknown}}
  ) {
    id
    commonName
    instruments {
      id
      isPrimary
      quotes {
        id
        isPrimary
        localTicker
        exchange {
          id
          shortName
          country {
            id
            countryCode
          }
        }
      }
    }
  }
}
    `;
